@import '../Variables/variables.scss';
@import '../Mixins/mixins.scss';

footer {
  height: 140px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: $darkgrey;
  padding: 2%;
  font-weight: bold;
}

footer ul {
  @include flexCenter(row);
  margin: 0;
  padding: 0;
  list-style-type: none;
}

footer ul li {
  padding: 0 4%;
}

footer ul li:hover {
  cursor: pointer;
}

footer ul li a {
  padding-bottom: 80px;
}

.footer-home {
  background: url('../../assets/footer/home.svg') center center no-repeat;
}

.footer-work {
  background: url('../../assets/footer/work.svg') center center no-repeat;
}

.footer-contact {
  background: url('../../assets/footer/contact.svg') center center no-repeat;
}

.footer-github {
  background: url('../../assets/footer/github.svg') center center no-repeat;
}

.footer-home:hover {
  background: url('../../assets/footer/activehome.svg') center center no-repeat;
}

.footer-work:hover {
  background: url('../../assets/footer/activework.svg') center center no-repeat;
}

.footer-contact:hover {
  background: url('../../assets/footer/activecontact.svg') center center no-repeat;
}

.footer-github:hover {
  background: url('../../assets/footer/activegithub.svg') center center no-repeat;
}